AGE,cAAA,CAFA,UAEA,CAHF,uCAEE,YACA,CAGF,+BAGE,cAAA,CAFA,eAEA,CAHF,qCAEE,eACA,CAMF,kSAIE,qBAAA,CAIJ,qBACE,QACA,CAFF,+BAEE,UAAA,CAFF,+BAEE,SAAA,CAGF,oBAEE,WAAA,CADA,UACA,CAAA","sourcesContent":["// Variables\n$breakpoint-small: 768px;\n$breakpoint-kindle: 800px;\n$breakpoint-medium: 992px;\n$breakpoint-tablet: 1024px;\n$breakpoint-1200: 1200px;\n$breakpoint-1150: 1150px;\n$breakpoint-1280: 1280px;\n\n// Common styles for both banners\n.banner-container {\n  position: relative;\n  width: 100%;\n  margin: 0px auto 20px;\n  padding: 0 30px;\n  border-radius: 4px;\n  overflow: hidden;\n\n  .banner-background {\n    width: 240px;\n    height: 240px;\n    object-fit: cover;\n    margin-right: auto;\n  }\n\n  .banner-content-wrapper {\n    display: flex;\n    align-items: center;\n    gap: 20px;\n  }\n\n  .banner-image {\n    width: 240px;\n    height: 240px;\n    margin: 0 15px 0 0;\n    object-fit: cover;\n    transition: all 0.3s ease;\n  }\n\n  .banner-text-container {\n    flex: 1;\n  }\n\n  .banner-label {\n    max-width: 800px;\n    font-size: 30px;\n    font-weight: bold;\n    margin-bottom: 10px;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    transition: color 0.3s ease;\n  }\n\n  .banner-description {\n    font-size: 18px;\n    margin-bottom: 10px;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    transition: color 0.3s ease;\n  }\n\n  .banner-tag {\n    display: block;\n    font-size: 15px;\n    margin-bottom: 20px;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    font-weight: bold;\n    transition: color 0.3s ease;\n  }\n\n  .banner-button {\n    display: inline-block;\n    padding: 10px;\n    color: #fff;\n    font-weight: bold;\n    text-decoration: none;\n    border-radius: 5px;\n    transition: all 0.3s ease;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n\n    &:hover {\n      transform: translateY(-1px);\n    }\n  }\n\n 