ry-info);\n  }\n\n  &.variant-1 {\n    background-image: linear-gradient(\n      to left,\n      transparent,\n      var(--banner-secondary-light) 25%,\n      var(--banner-secondary-bg) 50%,\n      var(--banner-secondary-dark) 80%\n    );\n  }\n\n  &.variant-2 {\n    background-image: linear-gradient(\n      to left,\n      transparent,\n      var(--banner-secondary-light) 40%,\n      var(--banner-secondary-bg) 70%,\n      var(--banner-secondary-dark) 100%\n    );\n  }\n}\n\n// Context menu styles\n.banner-context-menu {\n  position: absolute;\n  top: 15px;\n  right: 15px;\n  z-index: 10;\n}\n\n.banner-menu-button {\n  background: var(--color-dropdown-background);\n  border: none;\n  cursor: pointer;\n  width: 36px;\n  height: 36px;\n  border-radius: 50%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  color: var(--banner-primary-info);\n  transition: all 0.3s ease;\n  box-shadow: 0 2px 6px var(--color-banner-button-shadow);\n  padding: 0;\n\n  &:hover {\n    background: var(--color-dropdown-background);\n    color: var(--banner-primary-button-hover);\n  }\n}\n\n.banner-menu-dropdown {\n  position: absolute;\n  top: calc(100% + 5px);\n  right: 0;\n  background-color: var(--color-dropdown-background);\n  border-radius: 6px;\n  box-shadow: 0 2px 6px var(--color-banner-button-shadow);\n  overflow: hidden;\n  min-width: 150px;\n  backdrop-filter: blur(8px);\n  z-index: 20;\n  padding: 2px 0;\n}\n\n.banner-menu-item {\n  display: block;\n  width: 100%;\n  padding: 10px 16px;\n  text-align: left;\n  background: none;\n  border: none;\n  font-size: 14px;\n  font-weight: 500;\n  cursor: pointer;\n  transition: all 0.2s ease;\n  color: var(--color-menu-item-color);\n\n  &:hover {\n    background-color: var(--color-menu-item-hover);\n  }\n}\n\n// Responsive styles for tablets\n@media (max-width: $breakpoint-tablet) and (min-width: #{$breakpoint-small + 1}) {\n  .banner-container {\n    padding: 20px;\n\n    .banner-image {\n      width: 200px;\n      